.container {
  min-height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(-45deg, #ff335e, #f6ff33, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  animation: gradient 60s ease infinite;
  padding: 20px;
}

.content {
  text-align: center;
  color: white;
  padding: 2rem;
  opacity: 100%;
  border-radius: 25px;
  width: 90%;
  max-width: 800px;
  backdrop-filter: blur(8px);
}

.name {
  font-size: 3rem;
  margin-bottom: 0.5rem;
}

.subtitle {
  font-size: 1.5rem;
  margin-bottom: 2rem;
  opacity: 0.9;
}

.social-links {
  display: flex;
  justify-content: center;
  gap: 1.5rem;
  margin-bottom: 3rem;
}

.social-icon {
  color: white;
  font-size: 2rem;
  transition: transform 0.3s ease;
}

.social-icon:hover {
  transform: scale(1.2);
}

.social-icon img {
  width: 32px;
  height: 32px;
  filter: invert(1);
}

.carousel-container {
  overflow: hidden;
  width: 100%;
  margin: 0 auto;
}

.carousel-track {
  display: flex;
  transition: transform 0.5s ease-in-out;
}

.skill-card {
  flex: 0 0 100%;
  min-width: 100%;
  opacity: 0;
  transform: translateX(100%);
  transition: all 0.5s ease-in-out;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.skill-card.active {
  opacity: 1;
  transform: translateX(0%);
}

.skill-card.next {
  opacity: 0.5;
  transform: translateX(-100%);
}

.skill-icon {
  width: 100px;
  height: 100px;
  margin-bottom: 1rem;
  filter: drop-shadow(0 0 10px rgba(255,255,255,0.5));
}

.skill-icon.svg-inline--fa {
  font-size: 4rem;
}

.skill-name {
  font-size: 1.5rem;
  margin: 0;
}

@keyframes gradient {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

@media (max-width: 768px) {
  .name {
    font-size: 2rem;
  }

  .subtitle {
    font-size: 1.2rem;
  }

  .skill-icon {
    width: 80px;
    height: 80px;
  }

  .social-icon {
    font-size: 1.5rem;
  }
}